<template>
  <div id="wrap">
    <section-title sec-title="常见问题"></section-title>
    <tabs :paths="paths"></tabs>
    <div>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  import sectionTitle from '../others/section-title'
  import tabs from '../others/tabs'
  export default {
    name: 'problem',
    components: {
      sectionTitle,
      tabs
    },
    data () {
      return {
        paths:[
          {
            title: '全部问题',
            path: '/problem/all'
          },
          {
            title: '发布问题',
            path: '/problem/publish'
          }
        ]
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #wrap{
    position: relative;
    background-color: #fff;
    width: 1030px;
    height: 968px;
    margin: 22px 0 0 26px;
  }
</style>



